<template>
  <section class="main">
    <universal-filters v-model="filters" class="main__filters" save-to-url />
    <div class="main__content">
      <universal-search hide-filters :filters="filters" @update:filters="filters = $event" />
    </div>
    <sidebar class="main__sidebar" />
  </section>
</template>

<script>
import Sidebar from '@/components/HomeSidebar';
import UniversalSearch from '@/components/UniversalSearch/UniversalSearch.vue';
import UniversalFilters from '../components/UniversalSearch/UniversalFilters.vue';

export default {
  components: {
    Sidebar,
    UniversalSearch,
    UniversalFilters,
  },
  data() {
    return {
      filters: {},
      ios: {
        new: [],
        top: [],
      },
      cydia: {
        new: [],
        top: [],
      },
      tvos: {
        new: [],
        top: [],
      },
    };
  },
  computed: {
    title() {
      return this.$t('SITE_TITLE');
    },
  },
  serverPrefetch() {
    return Promise.allSettled(this.getApps());
  },
  created() {
    this.getApps();
  },
  methods: {
    getApps() {
      const promises = [];
      ['ios', 'cydia', 'tvos'].forEach((type) => {
        ['new', 'top'].forEach(((tab) => {
          promises.push(
            this.$api.search({ type, order: tab === 'new' ? 'added' : 'clicks_day' }).then((data) => {
              this[type][tab] = data.slice(0, 24);
            }),
          );
        }));
      });
      return promises;
    },
  },
};
</script>

